import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import CurrencyInput from 'react-currency-input-field';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import { toast } from 'react-toastify';
export default function RedeemMF(props) {
    const [loading, setLoading] = React.useState(false);
    const FUNDDATA = props.location.state.fundData
    
    React.useEffect(()=>{
        console.log(FUNDDATA)
    },[])
    
    const Amount = () => {
        const [amount, setAmount] = React.useState();
        const [isChecked, setIsChecked] = React.useState(false);

        React.useEffect(()=>{
            if(isChecked){
                setAmount(FUNDDATA.MFBalance)
            }
        },[isChecked])

        const createOrder = async () => {
            setLoading(true)
            // const securities = [{ "ticker": FUNDDATA.FundCode, "quantity": Number(amount/FUNDDATA.NAV).toFixed(0) + "", type: "SELL" }];
            // console.log(securities)
            // await executeSmallCaseTransaction({
            //     navigation: props.history,
            //     securities: securities,
            //     amount: amount, 
            //     isSIP: false, 
            //     isUnitsBased: false, 
            //     units: 0, 
            //     sipDate: 0, 
            //     SCREENTYPE: 1, 
            //     FUNDDATA: FUNDDATA, 
            //     isInvest: false
            // })
            setLoading(false)
        }
    
        const onRedeemClick = () => {
            if(amount && amount!== 0){
                if(amount > FUNDDATA.MintboxBalance){
                    toast.error(`You can redeem maximum ${CurrencyFormat(FUNDDATA.MintboxBalance,'long',2)}`)
                } else {
                    createOrder()
                }
            }
        } 
        return (
            <>
                <div style={{ margin: '4%' }}>
                    <div className='header1dark' style={{ paddingTop: '4%' }}>
                        Redemption amount
                    </div>
                    <div className='smallTextLight'>
                        Enter the amount to redeem
                    </div>
                    <CurrencyInput
                        className='normalInput rupee'
                        id="amount"
                        name="amount"
                        placeholder="10,000"
                        value={amount}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        onValueChange={(value) => {setIsChecked(false);setAmount(value)}}
                        intlConfig={{ locale: 'en-IN', currency: 'INR' }}
                    />
                    <div style={{ marginTop: "4%", display: "flex" }}>
                        <input
                            type="checkbox"
                            id="checkbox"
                            name="checkbox"
                            checked={isChecked}
                            onChange={() => setIsChecked(!isChecked)}
                            className='greenCheckbox'
                        />
                        <label htmlFor="checkbox" style={{ flex: 1 }}>
                            Fully redeem this mutual fund
                        </label>
                    </div>
                </div>
                <div className='bodyTextLight' style={{ position: "fixed", bottom: 70, width: "100%",padding: "2% 4%", borderTop: "1px solid "+cssVar.getPropertyValue('--light') }}>
                    Note: The actual amount redeemed may be slightly different due to constantly changing prices
                </div>
                <div className='footer'>
                    <button
                        className='outlineButton'
                        style={{color:cssVar.getPropertyValue('--red'),borderColor:cssVar.getPropertyValue('--red')}}
                        onClick={()=>onRedeemClick()}
                    >
                        Confirm and redeem
                    </button>
                </div>
            </>
        )
    }

    const Units = () => {
        const [units, setUnits] = React.useState();
        const [isChecked, setIsChecked] = React.useState(false);

        React.useEffect(()=>{
            if(isChecked){
                setUnits(FUNDDATA.Units)
            }
        },[isChecked])

        const createOrder = async () => {
            setLoading(true)
            // const securities = [{ "ticker": FUNDDATA.FundCode, "quantity": units + "", type: "SELL" }];
            // console.log(securities)
            // await executeSmallCaseTransaction({
            //     navigation: props.history,
            //     securities: securities,
            //     amount: 0, 
            //     isSIP: false, 
            //     isUnitsBased: true, 
            //     units: units, 
            //     sipDate: 0, 
            //     SCREENTYPE: 1, 
            //     FUNDDATA: FUNDDATA, 
            //     isInvest: false
            // })
            setLoading(false)
        }
    
        const onRedeemClick = () => {
            if(units && units!== 0){
                 if(units > FUNDDATA.Units){
                    toast.error(`You can redeem maximum ${FUNDDATA.Units} units`)
                } else {
                    createOrder()
                }
            }
        } 
        return (
            <>
                <div style={{ margin: '4%' }}>
                    <div className='header1dark' style={{ paddingTop: '4%' }}>
                        Redemption units
                    </div>
                    <div className='smallTextLight'>
                        Enter the number of units to redeem
                    </div>
                    <CurrencyInput
                        className='normalInput'
                        id="amount"
                        name="amount"
                        placeholder="100"
                        value={units}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        onValueChange={(value) => {setIsChecked(false);setUnits(value)}}
                    />
                    <div style={{ marginTop: "4%", display: "flex" }}>
                        <input
                            type="checkbox"
                            id="checkbox"
                            name="checkbox"
                            checked={isChecked}
                            onChange={() => setIsChecked(!isChecked)}
                            className='greenCheckbox'
                        />
                        <label htmlFor="checkbox" style={{ flex: 1 }}>
                            Fully redeem this mutual fund
                        </label>
                    </div>
                    {
                        units>0?
                        <div className='BodyTextDark secondaryGreen' style={{marginTop:'4%'}}>Approximate amount: {CurrencyFormat(units*FUNDDATA.NAV, 'long',2)}</div>
                        :
                        <></>
                    }
                </div>
                <div className='bodyTextLight' style={{ position: "fixed", bottom: 70, width: "100%",padding: "2% 4%", borderTop: "1px solid "+cssVar.getPropertyValue('--light') }}>
                    Note: The actual amount redeemed may be slightly different due to constantly changing prices
                </div>
                <div className='footer'>
                    <button
                        className='outlineButton'
                        style={{color:cssVar.getPropertyValue('--red'),borderColor:cssVar.getPropertyValue('--red')}}
                        onClick={()=>onRedeemClick()}
                    >
                        Confirm and redeem
                    </button>
                </div>
            </>
        )
    }

    const keyTitles = [
        { title: "Redeem amount", view: Amount()},
        { title: "Redeem units", view: Units() },
    ];

    if (loading) {
        return (
            <div style={{ flex: 1 }}>
                <MainHeader
                    header="Redeem"
                    navigation={props.history}
                />
                <ActivityLoaderMintbox />
            </div>
        );
    }
    else return (
        <div className='mainContainer'>
            <MainHeader
                navigation={props.history}
                header="Redeem"
            />
            <div className="dataContainer px-0">
                <div style={{ display: 'flex', alignItems: "center", gap: '4%', padding: '4%' }}>
                    <AMCIcon amcName={FUNDDATA.AMCName} height="40px" width="40px" />
                    <div style={{ flex: 1 }}>
                        <div className='header1dark'>
                            {FUNDDATA.SchemeName}
                        </div>
                        <div className='smallTextLight' style={{paddingTop:'1%'}}>Total available units: {FUNDDATA.Units}</div>
                        <div className='smallTextLight' style={{paddingTop:'1%'}}>NAV: <span>{CurrencyFormat(FUNDDATA.NAV,'long',2)}</span></div>
                    </div>
                </div>
                <Tabs tabsObject={keyTitles} color={cssVar.getPropertyValue('--darkGreen')}/>
            </div>

        </div>
    );
}

